<template>
  <div class="player-screen" ref="playerScreen">
    <img
      :src="media"
      v-if="media && mediaType === 'image'"
      class="player-screen__media player-screen__media--image"
      ref="media"
    />
    <iframe
      :src="`${media}?autoplay=1&control=0&showinfo=0`"
      :width="1920 / 3"
      :height="1080 / 3"
      v-if="media && mediaType === 'ytEmbed'"
      class="player-screen__media"
      ref="media"
    />

    <video
      :width="1920 / 3"
      :height="1080 / 3"
      v-if="media && mediaType === 'customVideo'"
      class="player-screen__media"
      ref="media"
      muted
    >
      <source :src="media" type="video/mp4" />
    </video>

    <div
      class="cursor"
      ref="cursor"
      :style="{
        width: `${eraserSizeRadius * 2}px`,
        height: `${eraserSizeRadius * 2}px`
      }"
    ></div>

    <canvas
      v-show="fogEnabled"
      class="player-screen__canvas"
      ref="fogCanvas"
      width="960"
      height="540"
    >
    </canvas>
    <canvas
      v-show="fogEnabled"
      class="player-screen__canvas player-screen__canvas--draw"
      ref="drawingCanvas"
      @mousedown="startDraw"
      @mousemove="keepDrawing"
      @mouseleave="mouseLeave"
      @mouseup="stopDraw"
    >
    </canvas>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { lsPush } from '@/local-storage-mixin'
export default {
  name: 'PlayerScreenDuplicate',
  props: {
    media: {
      type: String,
      required: true
    },
    mediaType: {
      type: String,
      required: true
    }
  },
  computed: {
    ...mapState('screenData', ['fogEnabled'])
  },
  watch: {
    fogEnabled(enabled) {
      if (!enabled) {
        this.removeFogFromMap(this.fogContext)
      } else {
        this.addFogOnMap(this.fogContext)
      }
    }
  },
  data() {
    return {
      fogCanvas: null,
      fogContext: null,
      drawingCanvas: null, // used to draw the stuff on that will actually clear the fog on the fogCanvas
      drawingContext: null,
      mouseDown: false,
      eraserSizeRadius: 5,
      previousX: null,
      previousY: null,
      currentX: null,
      currentY: null
    }
  },
  mounted() {
    lsPush('fogOfWar', '')
    setTimeout(() => {
      this.sizeAllObjects()

      this.fogCanvas = this.$refs.fogCanvas
      this.fogContext = this.fogCanvas.getContext('2d')
      // this.fogContext.globalAlpha = 0.7 // set the fogContext to a lower alpha so you can see-through to the media

      this.drawingCanvas = this.$refs.drawingCanvas
      this.drawingContext = this.drawingCanvas.getContext('2d')
      // this.drawingContext.globalAlpha = 0.7 // set the fogContext to a lower alpha so you can see-through to the media

      if (this.fogEnabled) {
        this.addFogOnMap(this.fogContext)
      }
    }, 200)
  },
  methods: {
    mouseLeave() {
      this.mouseDown = false
      this.stopDraw()
      const el = this.$refs.cursor
      el.style.left = '-10000px'
      el.style.top = '-10000px'
    },
    moveMouseCursor(e) {
      const el = this.$refs.cursor
      const canvasRect = this.drawingCanvas.getBoundingClientRect() // we just need one of the canvases because they both have the same rect
      const x = e.clientX - canvasRect.left
      const y = e.clientY - canvasRect.top

      el.style.top = `${y}px`
      el.style.left = `${x}px`
    },
    sizeAllObjects() {
      const mediaRef = this.$refs.media
      this.$refs.playerScreen.style.width = `${mediaRef.width}px`
      this.$refs.playerScreen.style.height = `${mediaRef.height}px`

      const objectsToSize = [this.$refs.fogCanvas, this.$refs.drawingCanvas]
      objectsToSize.forEach(o => {
        o.width = mediaRef.width
        o.height = mediaRef.height
      })
    },
    addFogOnMap(theContext) {
      console.log('add')
      // just a plain black square
      theContext.beginPath()
      theContext.fillRect(0, 0, this.fogCanvas.width, this.fogCanvas.height)
      theContext.fillStyle = 'black'
      theContext.closePath()
      this.createFogImage()
    },
    removeFogFromMap(theContext) {
      // just a plain black square
      theContext.beginPath()
      theContext.clearRect(0, 0, this.fogCanvas.width, this.fogCanvas.height)
      theContext.closePath()
      this.createFogImage()
    },
    startDraw(e) {
      const x = e.clientX - this.drawingCanvas.getBoundingClientRect().left
      const y = e.clientY - this.drawingCanvas.getBoundingClientRect().top
      this.previousX = x
      this.previousY = y
      this.mouseDown = true
      console.log(x, y)
    },
    keepDrawing(e) {
      this.moveMouseCursor(e)

      if (this.mouseDown) {
        this.drawOnCanvas(e)
      }
    },
    drawOnCanvas(e) {
      const canvasRect = this.drawingCanvas.getBoundingClientRect() // we just need one of the canvases because they both have the same rect
      const x = e.clientX - canvasRect.left
      const y = e.clientY - canvasRect.top

      this.currentY = y - this.previousY
      this.currentX = x - this.previousX

      this.drawingContext.clearRect(0, 0, canvasRect.width, canvasRect.height)
      this.drawingContext.beginPath()
      this.drawingContext.rect(
        this.previousX,
        this.previousY,
        this.currentX,
        this.currentY
      )
      this.drawingContext.fillStyle = 'white'
      this.drawingContext.fill()
      this.drawingContext.closePath()
    },
    eraseFog(theContext) {
      // erase the square drawn in drawOnCanvas method and reset the X's and Y's afterwards
      theContext.globalCompositeOperation = 'destination-out' // this removed instead of adds stuff
      theContext.beginPath()
      theContext.rect(
        this.previousX,
        this.previousY,
        this.currentX,
        this.currentY
      )
      theContext.fill()
      theContext.closePath()

      this.previousY = null
      this.previousX = null
      this.currentY = null
      this.currentX = null

      this.drawingContext.clearRect(
        0,
        0,
        this.drawingCanvas.getBoundingClientRect().width,
        this.drawingCanvas.getBoundingClientRect().height
      )
    },
    stopDraw(e) {
      this.eraseFog(this.fogContext)
      this.mouseDown = false
      // reset the contexts to be able to draw again after erasing
      this.fogContext.globalCompositeOperation = 'source-over'
      this.createFogImage()
    },
    createFogImage() {
      setTimeout(() => {
        const img = this.fogCanvas.toDataURL('image/png')
        // this.dummy = img
        console.log(img)
        lsPush('fogOfWar', img)
      }, 500)
    }
  }
}
</script>
<style lang="scss" scoped>
.cursor {
  width: 5px;
  height: 5px;
  background: white;
  border: 1px solid black;
  // border-radius: 50%;
  position: absolute;
  z-index: 99999;
  transform: translate(-50%, -50%);
  pointer-events: none;
  top: -1000px;
  left: -1000px;
}
.player-screen {
  cursor: none;
  width: 500px;
  position: relative;

  &__canvas,
  &__media {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    // pointer-events: none;
    z-index: 1;
  }

  &__media--image {
    height: auto;
  }

  &__canvas {
    z-index: 2;
    opacity: 0.6;

    &--shadow {
      display: none;
    }
  }
}
</style>
