<template>
  <div class="fullscreen-wrapper" ref="wrapper">
    <div
      class="fogwrapper"
      v-if="ytEmbed"
      :style="backgroundScaleCss"
      style="transform-origin: center;"
    >
      <iframe
        class="fullscreen-wrapper__item fullscreen-wrapper__item--video fullscreen-wrapper__item--embed"
        width="560"
        height="315"
        :src="`${ytEmbed}?autoplay=1&control=0&showinfo=0&vq=large`"
        title="YouTube video player"
        frameborder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowfullscreen
      ></iframe>
      <img :src="fogOfWar" v-if="fogOfWar" class="fogofwar" />
    </div>
    <div
      class="fogwrapper"
      v-if="customVideo"
      :style="backgroundScaleCss"
      style="transform-origin: center;"
    >
      <video
        v-if="customVideo"
        width="560"
        height="315"
        autoplay
        loop
        controls
        class="fullscreen-wrapper__item fullscreen-wrapper__item--video fullscreen-wrapper__item--embed"
      >
        <source :src="customVideo" type="video/mp4" />
      </video>
      <img :src="fogOfWar" v-if="fogOfWar" class="fogofwar" />
    </div>

    <div class="fogwrapper" v-if="background" :style="backgroundScaleCss">
      <img
        :src="background"
        class="fullscreen-wrapper__item"
        :style="imageCss"
      />
      <img :src="fogOfWar" v-if="fogOfWar" class="fogofwar" :style="imageCss" />
    </div>

    <div v-if="handout" class="handout">
      <div class="handout__item handout__item">
        <img :src="handout" class="handout__image" />
      </div>
      <div class="handout__item handout__item--flipped">
        <img :src="handout" class="handout__image" />
      </div>
    </div>

    <div v-show="gridActive" class="grid" ref="grid">
      <div
        class="grid__square grid__square--dummy"
        ref="dummySquare"
        v-if="gridSquareAmount === 0"
        :style="gridStyle"
      ></div>
      <div
        class="grid__square"
        v-for="num in gridSquareAmount"
        :key="num"
        :style="gridStyle"
        :class="{ 'is--transparent': anythingInUse }"
      />
    </div>

    <player-initiative-list />
  </div>
</template>
<script>
import { lsAttach, lsPull } from '../local-storage-mixin'
import { mapState } from 'vuex'
import PlayerInitiativeList from '@/components/Player/InitiativeList'
export default {
  name: 'PlayerScreen',
  components: {
    PlayerInitiativeList
  },
  data() {
    return {
      gridSquareAmount: 0
    }
  },
  computed: {
    gridStyle() {
      return {
        'border-color': 'black'
      }
    },
    anythingInUse() {
      if (
        this.ytEmbed !== '' ||
        this.customVideo !== '' ||
        this.background !== ''
      ) {
        return true
      }

      return false
    },
    imageCss() {
      let css = `transform: rotate(${this.imageRotation}deg); transform-origin: center center;`
      if (this.bestFitStatus) {
        // override rotate with object fit. They dont really go together that well
        css = 'object-fit: contain; width: 100vw; height: 100vh;'
      }

      return css
    },
    backgroundScaleCss() {
      return `transform: scale(${this.backgroundScale /
        100}); transform-origin: center center;`
    },
    ...mapState('screenData', [
      'ytEmbed',
      'customVideo',
      'background',
      'handout',
      'backgroundScale',
      'fogOfWar',
      'imageRotation',
      'gridActive',
      'bestFitStatus'
    ])
  },
  mounted() {
    lsAttach('gridStatus', () => {
      this.$store.commit('screenData/setGridStatus', lsPull('gridStatus'))
    })
    lsAttach('ytEmbed', () => {
      this.$store.commit('screenData/addYoutubeEmbed', lsPull('ytEmbed'))
    })
    lsAttach('customVideo', () => {
      this.$store.commit('screenData/setCustomVideo', lsPull('customVideo'))
    })
    lsAttach('background', () => {
      this.$store.commit('screenData/setBackground', lsPull('background'))
    })
    lsAttach('fogOfWar', () => {
      this.$store.commit('screenData/addFogOfWar', lsPull('fogOfWar'))
    })
    lsAttach('imageRotation', () => {
      this.$store.commit('screenData/setImageRotation', lsPull('imageRotation'))
    })
    lsAttach('backgroundScale', () => {
      this.$store.commit(
        'screenData/setBackgroundScale',
        lsPull('backgroundScale')
      )
    })

    lsAttach('toggleBestFitImage', () => {
      this.$store.commit(
        'screenData/setBestFitStatus',
        lsPull('toggleBestFitImage')
      )
    })
    lsAttach('handout', () => {
      this.$store.commit('screenData/setHandout', lsPull('handout'))
    })

    this.determineGridSquareAmount()
  },
  methods: {
    determineGridSquareAmount() {
      // get the width and height of the screen
      // make one square of 1" by 1". Get its offsetHeight and Width
      // Determine how many squares we'll need to fit in the resolution of the screen presented to us
      const wrapper = this.$refs.wrapper
      const dummySquare = this.$refs.dummySquare

      const amountHorizontal = wrapper.offsetWidth / dummySquare.offsetWidth
      const amountVertical = wrapper.offsetHeight / dummySquare.offsetHeight
      this.gridSquareAmount = Math.ceil(amountHorizontal * amountVertical)
      this.gridSquareAmount += Math.ceil(this.gridSquareAmount * 0.5) // add sone extra squares
    }
  }
}
</script>
