  align-items: center;
  z-index: 99;

  &__item {
    width: 45%;
    height: 100%;
    text-align: center;

    &--flipped {
      transform: rotate(180deg);
    }
  }

  &__image {
    height: 100%;
  }
}

.fogwrapper {
  position: relative;
  display: inline-block;
  // position: absolute;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%, -50%);
}

.fogofwar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 999;
}

.grid {
  height: 100vh;
