<template>
  <div :class="{ 'is--dead': enemy.health <= 0 }" class="enemy">
    <div class="enemy__status status">
      <div class="status__number">#{{ num + 1 }}</div>
      <!-- <div class="status__shield">
        <i class="status__shield__icon icon fa fa-shield"></i>
        <span class="status__shield__number">{{ AC }}</span>
      </div> -->
      <div class="status__conditions">
        <el-tooltip
          effect="dark"
          content="Enemy is buffed?"
          placement="top-start"
        >
          <el-checkbox
            :checked="buffed"
            v-model="buffed"
            @change="revertBuffed"
            class="condition-checkbox"
          >
            <i class="icon fa fa-arrow-square-up"></i>
          </el-checkbox>
        </el-tooltip>
        <el-tooltip
          effect="dark"
          content="Enemy is debuffed?"
          placement="top-start"
        >
          <el-checkbox
            :checked="debuffed"
            v-model="debuffed"
            @change="revertDebuffed"
            class="condition-checkbox"
          >
            <i class="icon fa fa-arrow-square-down"></i>
          </el-checkbox>
        </el-tooltip>
      </div>
    </div>
    <div class="enemy__body">
      <el-form class="heal-form" @submit.prevent="setNewHealth">
        <div class="heal-form__input">
          <el-form-item>
            <el-input
              type="number"
              :placeholder="placeholderText"
              v-model="healthDecrease"
            ></el-input>
          </el-form-item>
        </div>
        <div class="heal-form__method">
          <el-radio
            class="method-radio method-radio--damage"
            v-model="method"
            label="damage"
            ><i class="icon fa fa-bolt"></i
          ></el-radio>
          <el-radio
            class="method-radio method-radio--healing"
            v-model="method"
            label="healing"
            ><i class="icon fa fa-heart"></i
          ></el-radio>
        </div>
      </el-form>
      <div class="enemy__health">{{ enemy.health }}/{{ enemy.maxHealth }}</div>
    </div>
  </div>
</template>
<script>
import { lsPush } from '@/local-storage-mixin'
export default {
  name: 'Enemy',
  props: {
    enemy: {
      type: Object,
      required: true
    },
    num: {
      type: Number,
      required: true
    },
    AC: {
      type: Number,
      required: true
    },
    initiative: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      healthDecrease: null,
      debuffed: false,
      buffed: false,
      method: 'damage'
    }
  },
  computed: {
    placeholderText() {
      return this.method === 'damage' ? 'Damage' : 'Healing'
    }
  },
  methods: {
    setNewHealth() {
      const hpChange =
        parseInt(this.healthDecrease) >= 0 ? parseInt(this.healthDecrease) : 0
      const newHealth =
        this.method === 'damage'
          ? this.enemy.health - hpChange
          : this.enemy.health + hpChange
      this.$store.commit('initiativeTracker/setEnemyHealth', {
        id: this.enemy.id,
        newHealth
      })

      lsPush('setEnemyHealth', {
        id: this.enemy.id,
        newHealth
      })

      this.healthDecrease = null
    },
    revertBuffed() {
      this.debuffed = false
      lsPush('enemyBuffedDebuffedStatus', {
        buffed: this.buffed,
        debuffed: this.debuffed,
        enemyId: this.enemy.id
      })
    },
    revertDebuffed() {
      this.buffed = false
      lsPush('enemyBuffedDebuffedStatus', {
        buffed: this.buffed,
        debuffed: this.debuffed,
        enemyId: this.enemy.id
      })
    }
  }
}
</script>
