<template>
  <div class="toolbar">
    <a
      class="toolbar__item toolbar__button"
      href="#"
      @click.prevent="openDialog('handout')"
    >
      <i class="fa fa-paperclip"></i>
    </a>
    <a
      class="toolbar__item toolbar__button"
      href="#"
      @click.prevent="openDialog('background')"
    >
      <i class="fa fa-image"></i>
    </a>
    <a
      class="toolbar__item toolbar__button"
      href="#"
      @click.prevent="openDialog('youtube embed')"
    >
      <i class="fab fa-youtube"></i>
    </a>
    <a
      class="toolbar__item toolbar__button"
      href="#"
      @click.prevent="openDialog('custom video')"
    >
      <i class="fa fa-video"></i>
    </a>
    <el-tooltip effect="dark" content="Toggle plain grid" placement="top">
      <a
        class="toolbar__item toolbar__button"
        href="#"
        @click.prevent="toggleGrid"
        :class="{ 'grid--inactive': !gridActive }"
      >
        <i class="far fa-th" />
      </a>
    </el-tooltip>

    <el-dialog
      v-model="dialogVisible"
      :title="activeScreen"
      width="30%"
      :before-close="closeDialog"
      append-to-body
    >
      <div v-if="activeScreen === 'handout'">
        <el-form>
          <div v-if="tempSource">
            <h3>Selected handout:</h3>
            <img :src="tempSource" v-if="tempSource" width="250" /><br />
            <el-button type="danger" @click.prevent="tempSource = ''"
              ><i class="fa fa-trash-alt"></i
            ></el-button>
            <br />
            <br />
          </div>
          <template v-else>
            <el-form-item label="Upload image">
              <input type="file" @change="fileChange($event)" />
            </el-form-item>

            <br /><br />
            OR
            <br />
            <el-form-item label="Enter image URL">
              <el-input type="text" v-model="handoutUrl" />
            </el-form-item>
          </template>
          <el-button type="primary" @click.prevent="showHandout"
            >Show handout</el-button
          >
        </el-form>
      </div>
      <div v-if="activeScreen === 'background'">
        <div v-if="tempSource">
          <h3>Selected background:</h3>
          <img :src="tempSource" v-if="tempSource" width="300" /><br />
          <el-form-item>
            <el-checkbox v-model="enableFogOfWar"
              >Fog of war enabled</el-checkbox
            >
          </el-form-item>
          <el-button type="danger" @click.prevent="tempSource = ''"
            ><i class="fa fa-trash-alt"></i
          ></el-button>
          <br />
          <br />
          <el-button type="primary" @click.prevent="showBackground"
            >Show background</el-button
          >
        </div>
        <div v-else>
          <el-form>
            <el-form-item label="From computer">
              <input type="file" @change="fileChange($event)" />
            </el-form-item>
            <el-form-item label="From URL">
              <el-input type="text" v-model="tempSource" />
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div v-if="activeScreen === 'youtube embed'">
        <el-form>
          <el-form-item label="Embed code">
            <el-input type="text" v-model="ytEmbedInput" />
            3Z6Mkfetay8
          </el-form-item>
          <el-form-item>
            <el-checkbox v-model="enableFogOfWar"
              >Fog of war enabled</el-checkbox
            >
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.prevent="addYtEmbed"
              >Add Youtube Embed</el-button
            >
          </el-form-item>
        </el-form>
      </div>
      <div v-if="activeScreen === 'custom video'">
        <template v-if="tempSource !== ''">
          <video width="300">
            <source :src="tempSource" type="video/mp4" />
          </video>
          <br />
          <el-form-item>
            <el-checkbox v-model="enableFogOfWar"
              >Fog of war enabled</el-checkbox
            >
          </el-form-item>
          <el-button type="primary" @click.prevent="showVideo">
            Show video
          </el-button>
        </template>
        <template v-else>
          <input type="file" @change="fileChange" /><br /><br />
        </template>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { lsPush } from '@/local-storage-mixin'
import { mapState } from 'vuex'
export default {
  name: 'DMScreenToolbar',
  data() {
    return {
      dialogVisible: false,
      activeScreen: null,
      handoutUrl: '',
      tempSource: '',
      ytEmbedInput: '',
      enableFogOfWar: true
    }
  },
  computed: {
    ...mapState('screenData', [
      'ytEmbed',
      'customVideo',
      'background',
      'handout',
      'gridActive'
    ])
  },
  methods: {
    toggleGrid() {
      this.$store.commit('screenData/setGridStatus', !this.gridActive)
      lsPush('gridStatus', this.gridActive)
    },
    openDialog(screen) {
      this.activeScreen = screen
      this.dialogVisible = true
    },
    closeDialog() {
      this.activeScreen = null
      this.dialogVisible = false
      this.enableFogOfWar = true
    },
    showHandout() {
      const handout = this.tempSource !== '' ? this.tempSource : this.handoutUrl
      if (handout) {
        this.$store.commit('screenData/setHandout', handout)
        lsPush('handout', handout)
        this.handoutUrl = ''
        this.tempSource = ''
        this.closeDialog()
      }
    },
    fileChange(event) {
      const file = event.target.files[0]
      const url = URL.createObjectURL(file)
      this.tempSource = url
    },
    showBackground() {
      this.$store.commit('screenData/setFogOfWarEnabled', this.enableFogOfWar)
      this.$store.commit('screenData/setBackground', this.tempSource)
      lsPush('background', this.tempSource)
      this.tempSource = ''
      this.closeDialog()
    },
    showVideo() {
      this.$store.commit('screenData/setFogOfWarEnabled', this.enableFogOfWar)
      this.$store.commit('screenData/setCustomVideo', this.tempSource)
      lsPush('customVideo', this.tempSource)
      this.tempSource = ''
      this.closeDialog()
    },
    addYtEmbed() {
      this.$store.commit('screenData/setFogOfWarEnabled', this.enableFogOfWar)
      const embedUrl = `https://www.youtube.com/embed/${this.ytEmbedInput}`
      this.$store.commit('screenData/addYoutubeEmbed', embedUrl)
      lsPush('ytEmbed', embedUrl)
      this.ytEmbedInput = ''
      this.closeDialog()
    }
  }
}
</script>
<style lang="scss" scoped>
.toolbar {
  display: flex;
  height: 100%;

  &__item {
    width: 25%;
    color: #e2e2e2;

    & + & {
      border-left: 1px solid #ccc;
    }
  }

  &__button {
    font-size: 35px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 90%;
    text-decoration: none;
  }
}

.grid--inactive {
  color: #ccc;
}
</style>
