<template>
  <el-dialog
    title="Set initiative order"
    v-model="diagVis"
    append-to-body
    @close="closeDialog"
    width="320px"
  >
    <el-form :label-position="'left'" label-width="150px">
      <el-form-item
        :label="item.name"
        :key="index"
        v-for="(item, index) in items"
      >
        <el-input
          type="number"
          size="small"
          @focus="setSelection"
          v-model="item.initiative"
          class="initiative-setter__input"
        />
        <a
          href="#"
          @click.prevent="rollForItem(item)"
          v-if="item.type === 'enemyGroup'"
          >Roll</a
        >
      </el-form-item>
    </el-form>
    <span class="dialog-footer">
      <el-button type="primary" @click.prevent="setInitiative"
        >Set order</el-button
      >
      <!-- <el-button @click="dialogVisible = false">Cancel</el-button> -->
    </span>
  </el-dialog>
</template>
<script>
import { lsPush } from '@/local-storage-mixin'
import _ from 'lodash'
import { mapState } from 'vuex'
export default {
  name: 'InitiativeSetter',
  data() {
    return {
      items: []
    }
  },
  props: {
    dialogVisible: {
      type: Boolean,
      required: true,
      default: false
    }
  },
  watch: {
    dialogVisible(status) {
      // when the dialog becomes visible, clone all the current players and enemies
      if (status) {
        this.items = _.cloneDeep([...this.activePlayers, ...this.enemies])
      }
    }
  },
  computed: {
    ...mapState('initiativeTracker', ['players', 'enemies']),
    diagVis() {
      return this.dialogVisible
    },
    initiativeList() {
      if (this.players.length) {
        return _.orderBy(
          [...this.activePlayers, ...this.enemies],
          ['initiative'],
          ['desc']
        )
      }

      return []
    },
    activePlayers() {
      return this.players.filter(p => {
        return p.disabled === false
      })
    }
  },
  methods: {
    closeDialog() {
      this.$emit('closed')
    },
    setInitiative() {
      this.items.forEach(item => {
        this.$store.commit('initiativeTracker/setInitiativeById', {
          id: item.id,
          initiative: item.initiative
        })
      })

      this.closeDialog()
      lsPush('initiativeList', this.initiativeList)
    },
    rollForItem(item) {
      item.initiative = this.randomInt(1, 20)
    },
    randomInt(min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min
    },
    setSelection(a) {
      a.target.select()
    }
  }
}
</script>
<style lang="scss">
.initiative-setter {
  &__input {
    text-align: center;
  }
}
</style>
