// import Todo from '@/models/Todo'
const state = {
  // ytEmbed: '3Z6Mkfetay8'
  ytEmbed: '',
  customVideo: '',
  background: '',
  backgroundScale: '100',
  handout: '',
  initiativeList: [],
  initiativePos: 'bottomright',
  fogOfWar: '',
  fogEnabled: true,
  imageRotation: 0,
  gridActive: true,
  bestFitStatus: false
}

const actions = {
  addItem({ commit }, { title }) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        commit('addToItems', { title })
        resolve()
      }, 1000)
    })
  }
}

const mutations = {
  setBestFitStatus(state, status) {
    state.bestFitStatus = status
  },
  setGridStatus(state, status) {
    state.gridActive = status
  },
  setImageRotation(state, deg) {
    state.imageRotation = deg
  },
  addToItems(state, item) {
    state.items.push(item)
  },
  addYoutubeEmbed(state, vid) {
    state.ytEmbed = vid
  },
  setCustomVideo(state, vid) {
    state.customVideo = vid
  },
  setBackground(state, img) {
    state.background = img
  },
  addFogOfWar(state, img) {
    state.fogOfWar = img
  },
  setFogOfWarEnabled(state, status) {
    state.fogEnabled = status
  },
  setHandout(state, img) {
    state.handout = img
  },
  setBackgroundScale(state, scale) {
    state.backgroundScale = scale
  },
  setInitiativeList(state, dto) {
    state.initiativeList = dto
  },
  updateListPosition(state, dto) {
    state.initiativePos = dto
  },
  setEnemyHealth(state, { id, newHealth }) {
    const enemyGroups = state.initiativeList.filter(
      item => item.type === 'enemyGroup'
    )

    enemyGroups.forEach(enemyGroup => {
      const enemy = enemyGroup.enemies.find(enemy => enemy.id === id)
      if (enemy) {
        enemy.health = newHealth
      }
    })
  },
  setEnemyStatus(state, { buffed, debuffed, enemyId }) {
    const enemyGroups = state.initiativeList.filter(
      item => item.type === 'enemyGroup'
    )
    enemyGroups.forEach(enemyGroup => {
      const enemy = enemyGroup.enemies.find(enemy => enemy.id === enemyId)
      if (enemy) {
        enemy.buffed = buffed
        enemy.debuffed = debuffed
      }
    })
  }
}

const getters = {}

export default {
  state,
  getters,
  mutations,
  actions,
  namespaced: true
}
