<template>
  <div>
    <el-dialog
      title="New enemy"
      append-to-body
      v-model="diagVis"
      @close="closeDialog"
    >
      <p>
        Make one ore more enemies of the same kind. This enemy will go as one
        group in the initiative order
      </p>
      <p>
        <strong>Example:</strong> When you add "Orc" with an amount of 4 with 20
        health you will get 4 orcs with 20 health which share initiative.
      </p>
      <el-form :label-position="'top'" style="margin-top: 1rem;">
        <el-row>
          <el-col :span="12">
            <el-form-item label="Name">
              <el-input
                placeholder="Name"
                v-model="enemy.name"
                clearable
                autofocus
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="1">
            &nbsp;
          </el-col>
          <el-col :span="11">
            <el-form-item label="Amount of enemies">
              <el-input-number
                placeholder="Enemy amount"
                v-model="enemy.amount"
                clearable
                @focus="setSelection"
              ></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="Health">
              <el-input-number
                placeholder="Health"
                v-model="enemy.health"
                clearable
                @focus="setSelection"
              ></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="1">
            &nbsp;
          </el-col>
          <el-col :span="11">
            <el-form-item label="Armor class of enemies">
              <el-input-number
                placeholder="Armor class"
                v-model="enemy.AC"
                clearable
                @focus="setSelection"
              ></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span class="dialog-footer">
        <el-button type="primary" @click.prevent="saveEnemyForm"
          >Add enemy</el-button
        >
        <el-button @click="resetEnemyForm">Cancel</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { uuidv4 } from '@/utils'
import { mapState } from 'vuex'
export default {
  name: 'InitiativeTrackerEnemyDialog',
  props: {
    dialogVisible: {
      type: Boolean,
      required: true,
      default: false
    }
  },
  data() {
    return {
      searchTerm: '',
      enemy: {
        name: '',
        health: 0,
        amount: 1,
        AC: 10
      }
    }
  },
  computed: {
    ...mapState('npc', ['npcs']),
    diagVis() {
      return this.dialogVisible
    }
  },
  methods: {
    closeDialog() {
      this.$emit('closed')
    },
    resetEnemyForm() {
      this.searchTerm = ''
      this.enemy.name = ''
      this.enemy.health = 0
      this.enemy.amount = 1
      this.enemy.npcId = false
      this.enemy.AC = 10
      this.id = false

      this.closeDialog()
    },
    saveEnemyForm() {
      const enemies = []
      for (let i = 0; i < this.enemy.amount; i++) {
        enemies.push({
          id: uuidv4(),
          name: this.enemy.name,
          type: 'enemy',
          health: this.enemy.health,
          maxHealth: this.enemy.health,
          buffed: false,
          debuffed: false
        })
      }

      // group the enemies into 1 group and save that group
      const enemyGroup = {
        id: uuidv4(),
        name: this.enemy.name,
        initiative: 0,
        AC: this.enemy.AC,
        type: 'enemyGroup',
        enemies
      }

      this.$store.commit('initiativeTracker/addEnemyGroupToTracker', enemyGroup)

      this.resetEnemyForm()
    },
    querySearch(queryString, cb) {
      let results = this.npcs

      results = this.npcs.filter(item => {
        return item.title.toLowerCase().includes(queryString.toLowerCase())
      })

      results.forEach(result => {
        result.autoCompleteTitle = result.title
      })
      // call callback function to return suggestions
      cb(results)
    },
    handleSelect(item) {
      if (item) {
        this.enemy.name = item.title
        this.enemy.AC = item.armorClass ? item.armorClass : 10
        this.enemy.health = item.hitpoints ? item.hitpoints : 0
        this.enemy.npcId = item.id
      }
    },
    setSelection(a) {
      a.target.select()
    }
  }
}
</script>
