<template>
  <div v-if="initiativeTrackerOpen" class="initiative-tracker">
    <div class="draggable-block__inner">
      <div class="draggable-block__header">
        <div class="draggable-block__header-drag">
          <h4 class="draggable-block__title">Initiative tracker</h4>
        </div>
        <div class="initiative-tracker__buttons">
          <el-tooltip
            effect="dark"
            content="Reset all initiative rolls and enemies"
            placement="top-start"
          >
            <a
              href
              @click.prevent="resetInitiativeList"
              class="initiative-tracker__button"
            >
              <i class="fa fa-sync-alt icon"></i>
            </a>
          </el-tooltip>
          <el-tooltip
            effect="dark"
            content="Set initiative order"
            placement="top-start"
          >
            <a
              href
              @click.prevent="initiativeSetterDialogVisible = true"
              class="initiative-tracker__button"
            >
              <i class="fa fa-dice-d20 icon"></i>
            </a>
          </el-tooltip>
          <el-tooltip
            effect="dark"
            content="Add enemy or NPC to tracker"
            placement="top-start"
          >
            <a
              href
              @click.prevent="enemyDialogVisible = true"
              class="initiative-tracker__button"
            >
              <i class="fa fa-user-plus icon"></i>
            </a>
          </el-tooltip>
        </div>
      </div>
      <div class="draggable-block__content">
        <div class="position-picker">
          <el-tooltip
            class="item"
            effect="dark"
            content="Move list to topleft"
            placement="top-end"
          >
            <a
              href="#"
              class="position-picker__square"
              :class="{ 'is--active': initiativePos === 'topleft' }"
              @click.prevent="moveList('topleft')"
            ></a>
          </el-tooltip>
          <el-tooltip
            class="item"
            effect="dark"
            content="Move list to topright"
            placement="top-start"
          >
            <a
              href="#"
              class="position-picker__square"
              :class="{ 'is--active': initiativePos === 'topright' }"
              @click.prevent="moveList('topright')"
            ></a>
          </el-tooltip>
          <el-tooltip
            class="item"
            effect="dark"
            content="Move list to bottomleft"
            placement="bottom-end"
          >
            <a
              href="#"
              class="position-picker__square"
              :class="{ 'is--active': initiativePos === 'bottomleft' }"
              @click.prevent="moveList('bottomleft')"
            ></a>
          </el-tooltip>
          <el-tooltip
            class="item"
            effect="dark"
            content="Move list to bottomright"
            placement="bottom-start"
          >
            <a
              href="#"
              class="position-picker__square"
              :class="{ 'is--active': initiativePos === 'bottomright' }"
              @click.prevent="moveList('bottomright')"
            ></a>
          </el-tooltip>

          <el-tooltip
            class="item"
            effect="dark"
            content="Hide the list temporarily"
          >
            <a
              href="#"
              @click.prevent="moveList('hide')"
              class="position-picker__square position-picker__hide-button"
              :class="{ 'is--active': initiativePos === 'hide' }"
            ></a>
          </el-tooltip>
        </div>
        <ul class="initiative-tracker" v-if="initiativeList.length">
          <li
            class="initiative-tracker__item"
            v-for="(item, index) in initiativeList"
            :key="index"
          >
            <enemy-group-item :item="item" v-if="item.type === 'enemyGroup'" />
            <player-item :item="item" v-if="item.type === 'player'" />
          </li>
        </ul>

        <el-button type="primary" @click.prevent="playerDialogVisible = true"
          >Add player</el-button
        >
        <el-dialog
          v-model="playerDialogVisible"
          title="Add player to list"
          width="30%"
          :before-close="closePlayerDialog"
          append-to-body
        >
          <el-form>
            <el-row>
              <el-col>
                <el-form-item label="Player name">
                  <el-input type="text" v-model="playerForm.name" />
                </el-form-item>
              </el-col>
              <el-col>
                <el-form-item label="Armor class">
                  <el-input type="number" v-model="playerForm.armorClass" />
                </el-form-item>
              </el-col>
              <el-col>
                <el-form-item label="Avatar url">
                  <el-input type="text" v-model="playerForm.avatar" />
                </el-form-item>
              </el-col>
              <el-col>
                <el-form-item>
                  <el-button type="primary" @click.prevent="addPlayerToList"
                    >Add</el-button
                  >
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </el-dialog>

        <enemy-dialog
          :dialog-visible="enemyDialogVisible"
          @closed="enemyDialogVisible = false"
        />

        <initiative-setter
          :dialog-visible="initiativeSetterDialogVisible"
          @closed="initiativeSetterDialogVisible = false"
        />
      </div>
    </div>
  </div>
</template>
<script>
import { uuidv4 } from '@/utils'
import { mapState } from 'vuex'
import { lsPush } from '@/local-storage-mixin'
import EnemyDialog from '@/components/InitiativeTracker/EnemyDialog'
import PlayerItem from '@/components/InitiativeTracker/PlayerItem'
import EnemyGroupItem from '@/components/InitiativeTracker/EnemyGroupItem'
import InitiativeSetter from '@/components/InitiativeTracker/InitiativeSetter'
import _ from 'lodash'

export default {
  name: 'InitiativeTrackerPopup',
  components: {
    EnemyDialog,
    PlayerItem,
    EnemyGroupItem,
    InitiativeSetter
  },
  data() {
    return {
      playerDialogVisible: false,
      enemyDialogVisible: false,
      initiativeSetterDialogVisible: false,
      initiativeTrackerOpen: true,
      playerForm: {
        name: '',
        initiative: 0,
        armorClass: 0,
        type: 'player',
        buffed: false,
        debuffed: false,
        avatar: '',
        disabled: false
      }
    }
  },
  computed: {
    initiativeList() {
      if (this.players && this.players.length) {
        return _.orderBy(
          [...this.players, ...this.enemies],
          ['initiative'],
          ['desc']
        )
      }

      return []
    },
    ...mapState('initiativeTracker', ['players', 'enemies']),
    ...mapState('screenData', ['initiativePos'])
  },
  methods: {
    addPlayerToList() {
      const dto = this.playerForm
      dto.id = uuidv4()
      this.$store.dispatch('initiativeTracker/addPlayerToStorage', dto)

      this.closePlayerDialog()
    },
    closePlayerDialog() {
      this.playerForm.name = ''
      this.playerForm.initiative = 0
      this.playerForm.armorClass = 0
      this.playerForm.type = 'player'
      this.playerForm.buffed = false
      this.playerForm.debuffed = false
      this.playerForm.avatar = ''
      this.playerForm.disabled = false
      this.playerDialogVisible = false
    },
    resetInitiativeList() {
      this.$store.commit('initiativeTracker/resetEnemyGroups')
      lsPush('initiativeList', null)
    },
    moveList(pos) {
      this.$store.commit('screenData/updateListPosition', pos)
      lsPush('updateListPosition', pos)
    }
  }
}
</script>
