import { lsPush, lsPull } from '@/local-storage-mixin'
import _ from 'lodash'

const state = {
  players: lsPull('players'),
  enemies: []
}

const actions = {
  addPlayerToStorage({ commit }, dto) {
    let players = lsPull('players')
    if (!players) {
      players = []
    }
    players.push(dto)
    lsPush('players', players)
    commit('fetchPlayersFromStorage')
  }
}

// mutations
const mutations = {
  updatePlayerStatusInStorage(state, { id, status }) {
    const player = state.players.find(p => p.id === id)
    if (player) {
      player.disabled = status
      lsPush('players', state.players)
    }
  },
  fetchPlayersFromStorage(state) {
    state.players = lsPull('players')
  },
  resetInitiativeTracker(state) {
    state.players = []
    state.enemies = []
  },
  setPlayersInTracker(state, dto) {
    state.players = dto
  },
  addEnemyGroupToTracker(state, dto) {
    state.enemies.push(dto)
  },
  resetEnemyGroups(state) {
    state.enemies = []
  },
  setInitiativeById(state, { id, initiative }) {
    const enemyIndex = state.enemies.findIndex(enemy => enemy.id === id)
    if (enemyIndex >= 0) {
      state.enemies[enemyIndex].initiative = parseInt(initiative)
    }

    // if it couldnt be found in the enemies, try the player
    const playerIndex = state.players.findIndex(player => player.id === id)
    if (playerIndex >= 0) {
      state.players[playerIndex].initiative = parseInt(initiative)
    }
  },
  setEnemyHealth(state, { id, newHealth }) {
    state.enemies.forEach(enemyGroup => {
      const enemy = enemyGroup.enemies.find(enemy => enemy.id === id)
      if (enemy) {
        enemy.health = newHealth
      }
    })
  }
}

const getters = {
  attackOrder: state => {
    return _.orderBy(state.attackOrder, ['initiative'], ['desc'])
  },
  players: state => state.defaultPlayerList
}

export default {
  state,
  getters,
  actions,
  mutations,
  namespaced: true
}
