<template>
  <div class="turn-order" v-if="initiativeList?.length" :style="listStyling">
    <ul class="turn-order-list">
      <li
        class="turn-order-list__item"
        v-for="item in initiativeList"
        :key="item.id"
      >
        <img :src="item.avatar" v-if="item.avatar" style="width: 50px;" />
        <div v-if="item.type == 'enemyGroup'" class="enemy-group">
          <i class="fas fa-dice-d20"></i> {{ item.initiative }} |
          {{ item.name }}
          <ul class="enemy-list" v-if="item.enemies.length">
            <li
              v-for="(enemy, index) in item.enemies"
              :key="index"
              class="enemy-list__item"
            >
              <div
                class="enemy-list__status"
                :class="
                  `enemy-list__status--is-${healthStatus(
                    enemy.health,
                    enemy.maxHealth
                  )}`
                "
              >
                <i class="status-icon fa fa-biohazard" v-if="enemy.debuffed" />
                <i class="status-icon fa fa-angel" v-if="enemy.buffed" />
              </div>
              #{{ index + 1 }}
            </li>
          </ul>
        </div>

        <div v-else>
          <i class="fas fa-dice-d20"></i> {{ item.initiative }} |
          {{ item.name }}
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import { lsAttach, lsPull } from '@/local-storage-mixin'
import { mapState } from 'vuex'
export default {
  name: 'PlayerInitiativeList',
  components: {},
  computed: {
    listStyling() {
      const bleed = 20
      switch (this.initiativePos) {
        case 'topleft':
          return `top: ${0 + bleed}px; left:${0 + bleed}px`
        case 'topright':
          return `top: ${0 + bleed}px; right:${0 + bleed}px`
        case 'bottomleft':
          return `bottom: ${0 + bleed}px; left:${0 + bleed}px`
        case 'bottomright':
          return `bottom: ${0 + bleed}px; right:${0 + bleed}px`
        case 'hide':
          return 'bottom: -99999px; right:-99999px'
      }

      return `top: ${0 + bleed}px; left:${0 + bleed}px`
    },
    ...mapState('screenData', ['initiativeList', 'initiativePos'])
  },
  mounted() {
    // listeners
    lsAttach('initiativeList', () => {
      this.$store.commit(
        'screenData/setInitiativeList',
        lsPull('initiativeList')
      )
    })

    lsAttach('setEnemyHealth', () => {
      this.$store.commit('screenData/setEnemyHealth', lsPull('setEnemyHealth'))
    })

    lsAttach('enemyBuffedDebuffedStatus', () => {
      this.$store.commit(
        'screenData/setEnemyStatus',
        lsPull('enemyBuffedDebuffedStatus')
      )
    })

    lsAttach('updateListPosition', () => {
      console.log(lsPull('updateListPosition'))
      this.$store.commit(
        'screenData/updateListPosition',
        lsPull('updateListPosition')
      )
    })
  },
  methods: {
    healthStatus(health, maxHealth) {
      /*
      Good/Undamaged (Full to only minor wounds)
      Moderately Wounded (Roughly one quarter total HP)
      Bloodied (Roughly Half total HP)
      Seriously/Severely Wounded (1/3 to ¼ total HP)
      Critically Wounded (Death may be imminent)
      */
      const percentage = (health * 100) / maxHealth
      if (percentage > 75) {
        return 'healthy'
      }

      if (percentage > 50) {
        return 'moderate'
      }
      if (percentage > 20) {
        return 'bloodied'
      }
      if (percentage > 0) {
        return 'critical'
      } else {
        return 'dead'
      }
    }
  }
}
</script>
