    }
  }
}

.enemy-group {
  width: 100%;
}

.enemy-list {
  padding-left: 0;

  &__item {
    font-size: 18px;
    padding: 0.3rem 0.75rem 0.3rem 0;
    list-style: none;
    display: flex;
    align-items: center;
  }

  &__status {
    margin-right: 1rem;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: lightskyblue;
    position: relative;

    .status-icon {
      position: absolute;
      top: 50%;
      left: 50%;
      font-size: 18px;
      color: black;
      transform: translate(-50%, -50%);
    }

    &--is-moderate {
      background-color: yellow;
    }
    &--is-moderate {
      background-color: #ffffbf;
    }
    &--is-bloodied {
      background-color: #fdae61;
    }
    &--is-critical {
      background-color: #d7191c;
    }
