<template>
  <div class="player-item" :class="{ 'is--inactive': item.disabled }">
    <div
      v-if="playerAvatar"
      class="player-item__avatar avatar"
      :class="{
        'is--placeholder': playerAvatar === './img/placeholder.png'
      }"
    >
      <div class="avatar__wrapper">
        <img :src="`${playerAvatar}`" class="avatar__image" />
      </div>
      <div class="avatar__initiative">{{ item.initiative }}</div>
      <div class="avatar__AC shield" v-if="item.armorClass > 0">
        {{ item.armorClass }}
      </div>
    </div>
    <span class="player-item__name">{{ item.name }}</span>
    <a href="#" @click.prevent="togglePlayerStatus">disable/enable</a>
  </div>
</template>
<script>
export default {
  name: 'PlayerItem',
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  computed: {
    playerAvatar() {
      return this.item.avatar
        ? this.item.avatar
        : 'https://upload.wikimedia.org/wikipedia/commons/8/89/Portrait_Placeholder.png'
    }
  },
  methods: {
    togglePlayerStatus() {
      this.$store.commit('initiativeTracker/updatePlayerStatusInStorage', {
        id: this.item.id,
        status: !this.item.disabled
      })
    }
  }
}
</script>
