<template>
  <div class="dmscreen">
    <div class="dmscreen__media">
      <div class="mediacontainer">
        <div class="mediacontainer__tools">
          <span class="mediacontainer__helptext" v-if="!handout && fogEnabled">
            Start erasing fog of war right away by drawing on your map below
          </span>
          <el-button
            type="danger"
            @click.prevent="removeHandout"
            v-if="handout"
          >
            <i class="fa fa-trash-alt"></i>
          </el-button>

          <template v-if="background">
            <el-button @click.prevent="rotateImage()">rot</el-button>
            <el-form @submit.prevent="">
              <el-form-item>
                <el-tooltip
                  effect="dark"
                  class="item"
                  content="Scale image % based"
                  placement="bottom"
                >
                  <el-input-number
                    placeholder="Scale"
                    v-model="backgroundScale"
                    clearable
                    @change="updateBackgroundScale"
                  ></el-input-number>
                </el-tooltip>
              </el-form-item>
            </el-form>
            <el-button type="danger" @click.prevent="removeBackground">
              <i class="fa fa-trash-alt"></i>
            </el-button>
            <el-tooltip
              effect="dark"
              class="item"
              :content="tooltipForBestFit"
              placement="bottom"
            >
              <el-button type="primary" @click.prevent="bestFitImage()">
                <i class="far fa-expand-wide" v-if="bestFitStatus"></i>
                <i class="far fa-compress-wide" v-else></i>
              </el-button>
            </el-tooltip>
            <el-tooltip
              effect="dark"
              class="item"
              content="Toggle fog of war"
              placement="bottom"
            >
              <el-button type="primary" @click.prevent="toggleFogOfWar()">
                <i class="fa fa-sun" v-if="fogEnabled"></i>
                <i class="fa fa-smoke" v-else></i>
              </el-button>
            </el-tooltip>
          </template>
          <template v-if="ytEmbed">
            <el-form @submit.prevent="">
              <el-form-item>
                <el-tooltip
                  effect="dark"
                  class="item"
                  content="Scale image % based"
                  placement="bottom"
                >
                  <el-input-number
                    placeholder="Scale"
                    v-model="backgroundScale"
                    clearable
                    @change="updateBackgroundScale"
                  ></el-input-number>
                </el-tooltip>
              </el-form-item>
            </el-form>
            <el-button type="danger" @click.prevent="removeYtEmbed">
              <i class="fa fa-trash-alt"></i>
            </el-button>
            <el-tooltip
              effect="dark"
              class="item"
              content="Toggle fog of war"
              placement="bottom"
            >
              <el-button type="primary" @click.prevent="toggleFogOfWar()">
                <i class="fa fa-sun" v-if="fogEnabled"></i>
                <i class="fa fa-smoke" v-else></i>
              </el-button>
            </el-tooltip>
          </template>
          <template v-if="customVideo">
            <el-form @submit.prevent="">
              <el-form-item>
                <el-tooltip
                  effect="dark"
                  class="item"
                  content="Scale image % based"
                  placement="bottom"
                >
                  <el-input-number
                    placeholder="Scale"
                    v-model="backgroundScale"
                    clearable
                    @change="updateBackgroundScale"
                  ></el-input-number>
                </el-tooltip>
              </el-form-item>
            </el-form>
            <el-button type="danger" @click.prevent="removeCustomVideo">
              <i class="fa fa-trash-alt"></i>
            </el-button>
            <el-tooltip
              effect="dark"
              class="item"
              content="Toggle fog of war"
              placement="bottom"
            >
              <el-button type="primary" @click.prevent="toggleFogOfWar()">
                <i class="fa fa-sun" v-if="fogEnabled"></i>
                <i class="fa fa-smoke" v-else></i>
              </el-button>
            </el-tooltip>
          </template>
        </div>
        <div class="mediacontainer__body">
          <img :src="handout" v-if="handout" class="mediacontainer__handout" />
          <player-screen-duplicate
            v-if="background"
            :media="background"
            media-type="image"
          />
          <player-screen-duplicate
            v-if="ytEmbed"
            :media="ytEmbed"
            media-type="ytEmbed"
          />
          <player-screen-duplicate
            v-if="customVideo"
            :media="customVideo"
            media-type="customVideo"
          />
        </div>
      </div>
    </div>
    <div class="dmscreen__tools">
      <dm-screen-toolbar />
    </div>
    <div class="dmscreen__sidebar">
      <initiative-tracker />
    </div>
  </div>
</template>

<script>
import gql from 'graphql-tag'
import { lsPush } from '../local-storage-mixin'
import { mapState } from 'vuex'
import DmScreenToolbar from '@/components/DmScreen/Toolbar'
import InitiativeTracker from '@/components/InitiativeTracker/InitiativeTracker'
import PlayerScreenDuplicate from '@/components/PlayerScreen/Duplicate'
export default {
  name: 'DMScreen',
  components: {
    InitiativeTracker,
    DmScreenToolbar,
    PlayerScreenDuplicate
  },
  data() {
    return {
      ytEmbedInput: '',
      handoutUrl: '',
      backgroundScale: 100,
      login: '',
      authItem: '',
      access_token: ''
    }
  },
  apollo: {
    login: {
      query: gql(`
      {
        login(email: "tengwerda@gmail.com", password: "waterfiets") {
          access_token
          token_type
          expires_in
        }
      }
    `),
      result({ data, loading }) {
        console.log(data)
        console.log(loading)
        if (data?.login) {
          data.login.forEach(l => {
            this.access_token = l.access_token
          })
        }
      }
      // pollInterval: 1000
    }
  },
  computed: {
    ...mapState('screenData', [
      'ytEmbed',
      'customVideo',
      'background',
      'handout',
      'fogEnabled',
      'imageRotation',
      'bestFitStatus'
    ]),
    tooltipForBestFit() {
      if (this.bestFitStatus) {
        return 'Use full size image'
      }

      return 'Make best fit'
    }
  },
  methods: {
    rotateImage() {
      let rotation = this.imageRotation + 90
      rotation = rotation >= 360 ? 0 : rotation
      this.$store.commit('screenData/setImageRotation', rotation)
      lsPush('imageRotation', rotation)
    },
    removeHandout() {
      this.$store.commit('screenData/setHandout', '')
      lsPush('handout', '')
    },
    removeYtEmbed() {
      this.ytEmbedInput = ''
      this.$store.commit('screenData/addYoutubeEmbed', '')
      lsPush('ytEmbed', '')
      lsPush('fogOfWar', '')
    },
    toggleFogOfWar(status) {
      const defStatus =
        status === false || status === true ? status : !this.fogEnabled
      this.$store.commit('screenData/setFogOfWarEnabled', defStatus)
      if (!this.fogEnabled) {
        lsPush('fogOfWar', '')
      }
    },
    removeCustomVideo() {
      this.$store.commit('screenData/setCustomVideo', '')
      lsPush('fogOfWar', '')
      lsPush('customVideo', '')
    },
    removeBackground() {
      this.$store.commit('screenData/setBackground', '')
      lsPush('background', '')
      lsPush('backgroundScale', '100')
      this.backgroundScale = 100

      this.toggleFogOfWar(false)
    },
    updateBackgroundScale() {
      lsPush('backgroundScale', this.backgroundScale)
    },
    bestFitImage() {
      this.$store.commit('screenData/setBestFitStatus', !this.bestFitStatus)
      lsPush('toggleBestFitImage', this.bestFitStatus)
    }
  }
}
</script>
